.antisnake {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr;

  @media (min-width: (2 * $block-height)) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width: (3 * $block-height)) {
    grid-template-columns: repeat(3, 1fr);
  }

  &__block {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-shrink: 0;

    height: $block-height;

    @for $i from 1 through 6 {
      &:nth-child(#{$i})::before {
        content: "#{$i}";
      }
    }

    @for $i from 1 through 6 {
      &:nth-child(#{$i}) {
        background-color: rgb(300 - $i * 51, 0, 0);
      }
    }
  }
}
